@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root{box-sizing:border-box}body{margin:0 auto;font-family:'roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;transition:0.5s}a{text-decoration:none;color:black}ul{list-style-type:circle}h1,h2,h3{text-align:center;font-weight:500;position:relative;z-index:1}h2{font-size:48pt;text-align:left;width:-webkit-min-content;width:min-content;white-space:nowrap}h1{font-size:64pt}p{font-weight:400}h3{-webkit-margin-before:0em;margin-block-start:0em;-webkit-margin-after:1em;margin-block-end:1em}img{max-width:100%}@media only screen and (max-width: 850px){h1{font-size:48pt}h2{margin:auto;padding-bottom:5rem;font-size:36pt}}@media only screen and (max-width: 375px){h3,h3 a{font-size:22pt}}

.header{display:flex;flex-wrap:nowrap;justify-content:space-around;background-color:#eee;transition:0.5s}.header button{margin:1em;font-size:12pt}.header a{text-decoration:none}.header__darkmode{text-align:right;width:20%}.header__logo{width:24%;text-align:left}.header__logo img{max-width:80px;width:50px;padding-left:1em;padding-top:1em}.header__logo img:hover{-webkit-animation:header__logo--spin 1s;animation:header__logo--spin 1s}.header__flag{width:30px}.darkmode .header{background-color:#111}.darkmode .header__logo img{-webkit-filter:invert(59%) sepia(12%) saturate(2484%) hue-rotate(128deg) brightness(99%) contrast(90%);filter:invert(59%) sepia(12%) saturate(2484%) hue-rotate(128deg) brightness(99%) contrast(90%)}@media only screen and (min-width: 1023px){.header__sidenav{display:none}}@media only screen and (max-width: 1023px){.header{justify-content:space-between}}

.d-nav{display:flex;flex-wrap:nowrap}.d-nav button{display:block;flex-direction:column;white-space:nowrap}.m-nav__button{display:block !important;margin-left:2em !important;margin-bottom:2em !important}.m-nav__sidenav{height:100%;width:0;position:fixed;z-index:2;top:0;right:0;background-color:#21b6ae;overflow-x:hidden;transition:0.5s;white-space:nowrap}.m-nav__sidenav a{padding:1em;display:block;transition:0.3s}.darkmode .m-nav__sidenav{background:#0a615d}

.round-button{cursor:pointer;border-radius:50%;width:50px;height:50px;background-color:#21b6ae;transition:.2s;display:flex;justify-content:center;align-items:center}.round-button:hover{background-color:#eee}

.greetings{background-color:#dfdf1b;transition:0.5s;text-align:center;padding-top:12em;padding-bottom:7em;position:relative}.greetings div{margin:auto}.greetings__button{-webkit-animation:greetings__button 3s infinite;animation:greetings__button 3s infinite;padding-bottom:3em}.greetings__header{position:relative}.greetings__tilt{position:absolute;left:0;width:100%;overflow:hidden;line-height:0;z-index:1}.greetings__tilt svg{position:relative;display:block;width:calc(100% + 1.3px);height:128px;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.greetings__tilt .greetings__shape-fill{fill:#eee;transition:0.5s}.greetings__triangle{position:absolute;left:0;width:100%;overflow:hidden;line-height:0;-webkit-transform:rotate(180deg);transform:rotate(180deg)}.greetings__triangle svg{position:relative;display:block;width:calc(100% + 1.3px);height:128px;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.greetings__triangle .greetings__shape-fill{fill:#eee;transition:.5s}.darkmode .greetings{background:url(https://wallpapercave.com/wp/wp2757874.gif)}.darkmode .greetings__tilt .greetings__shape-fill{fill:#111}.darkmode .greetings__triangle .greetings__shape-fill{fill:#171d3f}@-webkit-keyframes greetings__button{0%{-webkit-transform:translateY(0px);transform:translateY(0px)}50%{-webkit-transform:translateY(20px);transform:translateY(20px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px)}}@keyframes greetings__button{0%{-webkit-transform:translateY(0px);transform:translateY(0px)}50%{-webkit-transform:translateY(20px);transform:translateY(20px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px)}}

.about-me{display:flex;align-items:center;background:#eee}.about-me p{font-size:1.2rem}.about-me__header{width:50%;position:relative}.about-me__lower{display:block;text-align:left;padding-left:2em}.about-me__lower--polish{display:block;text-align:left;white-space:nowrap;width:-webkit-min-content;width:min-content}.about-me__lower--polish::after{content:'';height:40px;width:220px;left:-10px;top:50px;background-color:#21b6ae;position:absolute;z-index:-1}.about-me__lower::after{content:'';height:40px;width:200px;top:50px;left:-10px;background-color:#21b6ae;position:absolute;z-index:-1}.about-me__upper{text-align:left;display:block}.about-me__content{width:40%}@media only screen and (max-width: 850px){.about-me{flex-direction:column}.about-me__header,.about-me__content{width:100%;margin:1em}.about-me__lower{padding-left:2em}.about-me__lower::after{width:150px;top:30px}.about-me__lower--polish::after{width:180px;left:-10px;top:30px}}

.skills{background-color:#21b6ae;padding-bottom:5em !important}.skills--english::after{content:'';height:40px;left:-10px;width:180px;top:50px;background-color:#dfdf1b;position:absolute;z-index:-1}.skills--polish::after{content:'';height:40px;left:-10px;width:360px;top:50px;background-color:#dfdf1b;position:absolute;z-index:-1}.skills__container{grid-template-columns:repeat(4, 1fr);display:grid;justify-items:center;border-radius:20px}.skills__tile{box-sizing:border-box;border-radius:20px;margin:1em;width:60%;background:#eee;padding:1em}.skills__tile img{margin:20%}.skills__tile:hover{background:#ddd}@media only screen and (max-width: 1023px){.skills__container{grid-template-columns:repeat(2, 1fr)}}@media only screen and (max-width: 850px){.skills--english::after{height:40px;width:140px;top:30px}.skills--polish::after{height:40px;width:290px;top:30px}}@media only screen and (max-width: 400px){.skills__tile{box-sizing:border-box;border-radius:20px;margin-top:0em;width:80%;background:#eee;padding:0em}.skills__tile img{margin:1em}}

.m-projects{box-sizing:border-box;width:80%;margin:auto;padding:3em 0em 1em 0em}.m-projects__slide{position:relative}.m-projects__content{position:absolute;width:100%;height:100%;top:0;display:flex;justify-content:center;align-items:center}.m-projects__content a h3{padding-left:0;margin-bottom:0em;font-size:36pt}.m-projects__content a h4{margin:0.3em;text-align:center;font-size:24pt}

.my-projects{background:#eee;transition:.5s;padding-top:2em;padding-bottom:5em;position:relative}.my-projects p{font-size:12pt}.my-projects button::before{color:#333}.my-projects__project{box-sizing:border-box;border-radius:10px;padding:5em 3em;margin-top:2em;font-size:14pt;width:50%;display:inline-block}.my-projects--english::after{content:'';height:40px;width:350px;top:50px;left:-10px;background-color:#21b6ae;position:absolute;z-index:-1}.my-projects--polish::after{content:'';height:40px;width:400px;top:50px;left:-10px;background-color:#21b6ae;position:absolute;z-index:-1}@media only screen and (max-width: 850px){.my-projects--polish::after{width:310px;top:30px}.my-projects--english::after{width:270px;top:30px}}.darkmode .my-projects{background-color:#0a615d}.darkmode .my-projects__project{background-color:#2a302f}.darkmode .my-projects__triangle .my-projects__shape-fill{fill:#171d3f}

.contact{padding-top:10em !important;padding-bottom:5em !important;background:#dfdf1b}.contact h2{margin:auto !important;padding-bottom:5rem}.contact h2::after{content:'';height:40px;width:250px;left:-10px;top:50px;background-color:#eee;position:absolute;z-index:-1}.contact a{text-align:center}.contact__container{width:50%;margin:auto;font-size:1.2rem;text-align:center;padding-bottom:2rem}.contact__tilt{position:absolute;left:0;width:100%;overflow:hidden;line-height:0}.contact__tilt svg{position:relative;display:block;width:calc(100% + 1.3px);height:150px}.contact__tilt .contact__shape-fill{fill:#eee}@media only screen and (max-width: 850px){.contact h2::after{width:115%;top:30px}}.darkmode .contact__triangle .contact__shape-fill{fill:#0a615d}.darkmode .contact{background:#171d3f}.darkmode .contact__triangle{background:#171d3f}

.goup-button{position:fixed;bottom:20px;right:20px;transition:0.5s;display:none;z-index:20}.goup-button div{background-color:#111;color:#eee}.goup-button div:hover{background:#333;color:#eee}

.app{margin:0 auto}.my-projects,.contact,.about-me,.footer,.skills{padding:2em;padding-left:20%;padding-right:20%}.darkmode{background:#111;color:#eee}.darkmode a{color:#eee;text-decoration:underline}@media only screen and (max-width: 1680px){.my-projects,.contact,.about-me,.footer,.skills{padding:2em;padding-left:15%;padding-right:15%}}@media only screen and (max-width: 850px){.my-projects,.contact,.about-me,.header,.footer,.skills{padding:1em;padding-left:10%;padding-right:10%}}@-webkit-keyframes app__logo--spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes app__logo--spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@media (prefers-reduced-motion: no-preference){.app__logo{-webkit-animation:app__logo--spin infinite 20s linear;animation:app__logo--spin infinite 20s linear}}

